export default function HorizontalTimeline(props) {
  // const data = steps.map(({ leftText, rightText, icon }) => ({
  //   date: rightText,
  //   info: leftText,
  //   icon,
  // }));
  console.log(props.steps);

  return (
    <div className='horizontal-timeline'>
      <div className='horizontal-timeline__inner'>
        {props.steps.map((step) => (
          <HorizontalTimelineItem step={step} key={step._id} />
        ))}
      </div>
    </div>
  );
}
